import React from "react";
import styled, { keyframes } from "styled-components";
import PurchaseButton from "../../components/buttons/PurchaseButton";
import { H1, MediumText } from "../styles/TextStyles";
import { themes } from "../styles/ColorStyles";
import MockupAnimation from "../animations/MockupAnimation";
import WaveBackground from "../backgrounds/WaveBackground";

function HeroSection() {
  const screenType =
    typeof window !== `undefined`
      ? require("react-screentype-hook").default()
      : { isMobile: false };

  return (
    <Wrapper>
      <WaveBackground />
      <ContentWrapper>
        <AnimationWrapper>
          <SquareAnimation>
            <div class="square1" />
            <div class="square2" />
            <div class="square3" />
            <div class="square4" />
            <div class="square5" />
            <div class="square6" />
            <div class="square7" />
            <div class="square8" />
          </SquareAnimation>
        </AnimationWrapper>
        <TextWrapper>
          <Title>I am Saksham Khatod</Title>
          <Description>Coder. Designer. Photographer.</Description>
          <PurchaseButton title="Contact Me" subtitle="E-mail sender" />
        </TextWrapper>
        {!screenType.isMobile ? <MockupAnimation /> : null}
      </ContentWrapper>
    </Wrapper>
  );
}

export default HeroSection;

const Wrapper = styled.div`
  overflow: hidden;
  height: 950px;

  @media (max-width: 1200px) {
    height: 1400px;
  }
`;

const ContentWrapper = styled.div`
  max-width: 1234px;
  margin: 0 4%;
  padding: 200px 30px;
  padding-bottom: 260px;
  display: grid;
  grid-template-columns: 360px auto;

  @media (max-width: 1200px) {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 30px;
  }
`;

const TextWrapper = styled.div`
  max-width: 360px;
  display: grid;
  gap: 30px;
`;

const Title = styled(H1)`
  color: ${themes.dark.text1};
  background: linear-gradient(180deg, #c9cee8 0%, #a3c1ff 100%);

  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

const Description = styled(MediumText)`
  background: linear-gradient(180deg, rgba(48, 37, 84, 1), rgba(23, 8, 72, 1));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

const AnimationWrapper = styled.div`
  position: absolute;
  top: 130px;
  left: -20px;
`;

const SquareAnimation = styled.div`
  position: relative;
  transition: 2s cubic-bezier(0.075, 0.82, 0.165, 1);

  div {
    position: absolute;
    width: 28.125rem;
    height: 28.125rem;
    border: solid;
    border-width: 2px;
    border-color: rgba(255, 255, 255, 0.3);
    border-radius: 25%;
    animation: spin 20s linear infinite, morph 30s ease-in-out infinite;
    animation-duration: 20s;
    transform: rotate(0deg);
  }

  @media (max-width: 450px) {
    div {
      overflow: hidden;
    }
  }

  .square1 {
    animation-delay: 0.2s;
    opacity: 1;
  }

  .square2 {
    animation-delay: 0.4s;
    opacity: 0.8;
  }

  .square3 {
    animation-delay: 0.6s;
    opacity: 0.6;
  }

  .square4 {
    animation-delay: 0.8s;
    opacity: 0.4;
  }

  .square5 {
    animation-delay: 1s;
    opacity: 0.3;
  }

  .square6 {
    animation-delay: 1.2s;
    opacity: 0.2;
  }

  .square7 {
    animation-delay: 1.4s;
    opacity: 0.1;
  }

  .square8 {
    animation-delay: 1.6s;
    opacity: 0.05;
  }

  @keyframes spin {
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes morph {
    0% {
      border-radius: 25%;
    }
    20% {
      border-radius: 50%;
    }
    30% {
      border-radius: 45%;
    }
    40% {
      border-radius: 25%;
    }
    50% {
      border-radius: 45%;
    }
    60% {
      border-radius: 25%;
    }
    70% {
      border-radius: 45%;
    }
    80% {
      border-radius: 50%;
    }
    100% {
      border-radius: 25%;
    }
  }
`;
